				"html": "<h2>What is Ruby?</h2>\n<p><a>Ruby</a> is an interpreted scripting language for quick and easy object-oriented programming. Which means:</p>\n<ul>\n<li>Ability to make operating system calls directly</li>\n<li>Powerful string operations and regular expressions</li>\n<li>Immediate feedback during development</li>\n<li>Variable declarations are unnecessary</li>\n<li>Variables are not typed</li>\n<li>Syntax is simple and consistent</li>\n<li>Memory management is automatic</li>\n<li>Everything is an object</li>\n<li>Classes, methods, inheritance, etc.</li>\n<li>Singleton methods</li>\n<li>Has \"mixin\" functionality by module</li>\n<li>Iterators and closures</li>\n</ul>\n<p>If you are unfamiliar with some of the concepts above, read on, and don't worry. The mantra of the ruby language is quick and easy.</p>\n<h2>Version</h2>\n<p>The current stable version is 2.3.0. It has various performance improvements and syntactic additions from its predecessor, ruby v2.2.4.</p>\n<h2>Installation</h2>\n<p>Mac OS X and many Linux distributions come pre-installed with Ruby. To check if ruby is pre-installed in your system, just run <code>ruby -v</code> on your shell. There are several ways to install Ruby:</p>\n<ul>\n<li>When you are on a UNIX-like operating system, using your system’s package manager is the easiest way of getting started. However, the packaged Ruby version usually is not the newest one.</li>\n<li>Installers can be used to install a specific or multiple Ruby versions. There is also an installer for Windows.</li>\n<li>Managers help you to switch between multiple Ruby installations on your system.</li>\n<li>And finally, you can also build Ruby from source.</li>\n</ul>\n<p>To know about how to install Ruby through package managers, installers and source, click <a href=\"https://www.ruby-lang.org/en/documentation/installation/\">here</a>. RVM (Ruby Version Manager) and rbenv are the most popular Ruby managers to manage multiple Rubies. If you get stuck anywhere, don't worry, just head over to our <a href=\"https://gitter.im/FreeCodeCamp/ruby\">Gitter chat room</a> and ask us anything.</p>\n<h2>IRB</h2>\n<p>IRB stands for Interactive Ruby Shell. The abbreviation irb comes from the fact that the filename extension for Ruby is \".rb\", although interactive Ruby files do not have an extension of \".irb\". The program is launched from a command line and allows the execution of Ruby commands with an immediate response, experimenting in real-time. It features command history, line editing capabilities, and job control, and is able to communicate directly as a shell script over the Internet and interact with a live server. It was developed by Keiju Ishitsuka.</p>\n<pre><code>irb\n2.3.0 :001 > print \"Hello World\"\nHello World! => nil\n</code></pre>\n<h2>Ruby Interpreter</h2>\n<p>The Ruby interpreter is what is used to run Ruby scripts. If it is available and in Unix shell’s search path makes it possible to start it by typing the command <code>ruby</code> followed by the script name will invoke the interpreter and run the script.</p>\n<p><code>hello_campers.rb</code></p>\n<pre><code>if 'welcome' == 'welcome'\n    print('Hello campers!')\nend\n</code></pre>\n<p>From command-line:</p>\n<pre><code>$ ruby hello_campers.rb\nHello campers!\n</code></pre>\n<h2>Documentation</h2>\n<p>Ruby is well <a href=\"https://www.ruby-lang.org/en/documentation/\">documented</a>. These docs include tutorials, guides, references and meta information for language.<br>\nAnother important resouce for documentation is <a href=\"http://ruby-doc.org/core-2.3.0/\">Ruby Doc</a>. You should visit this <a href=\"https://github.com/airbnb/ruby\">link</a> to know more about Ruby style guide, written by developers of AirBnB.</p>\n<h2>Debugging</h2>\n<p>Inline <code>print</code> statements can be used for simple debugging:</p>\n<pre><code>print some_variable # prints to console\n</code></pre>\n<blockquote>\n<p><strong>... often the quickest way to debug a program is to add a few print statements to the source: the fast edit-test-debug cycle makes this simple approach very effective.</strong></p>\n</blockquote>\n<p>Ruby also includes more powerful tools for debugging, such as:  </p>\n<ul>\n<li><a href=\"https://github.com/nixme/pry-debugger\"><em>pry-debugger</em></a></li>\n</ul>\n<h2>Hello World!</h2>\n<p>Going back to the docs, we can read about the <a href=\"http://ruby-doc.org/core-2.3.0/Kernel.html#method-i-print\"><code>print</code></a> method, one of the built-in methods of the <a href=\"http://ruby-doc.org/core-2.3.0/Kernel.html\">the Kernel module</a>.  </p>\n<pre><code>print(obj, ...) → nil\n</code></pre>\n<p>Prints each object to $stdout. Objects that aren’t strings will be converted by calling their <code>to_s</code> method. The return value of print is <code>nil</code>. So when you run <code>print \"Hello World!</code> in your IRB. The output is:</p>\n<pre><code>2.3.0 :001 > print \"Hello World!\"\nHello World!\n => nil\n</code></pre>\n<h2>Frameworks</h2>\n<p>Ruby has several frameworks for quickly scaffolding applications. The most popular by far is <a href=\"http://rubyonrails.org/\">Rails</a> which was initially released in 2004. Other frameworks for Ruby include <a href=\"http://www.sinatrarb.com/\">Sinatra</a>, <a href=\"http://lotusrb.org/\">Lotus</a>, and <a href=\"http://voltframework.com/\">Volt</a>. Each of these options has their pros and cons for development and cater to a variety needs.</p>",
